            <BlockCode codeString={`void loop() {
  unsigned long currentMillis = millis();
  if (currentMillis - previousMillis >= interval) {
    mess_api = "";
    do_va_gui_messMqtt();
    gui_data_api_history();
    previousMillis = currentMillis;
  }
}` } language={'javascript'} path={'loop'} />
